/* CSS related to text - fonts, text-related animations, etc */

@font-face {
  font-family: "coolvetica";
  src: local("coolvetica"), url("../fonts/coolvetica.otf") format("truetype");
}

@font-face {
  font-family: "coolvetica_comp";
  src: local("coolvetica_comp"), url("../fonts/coolvetica_comp.otf") format("truetype");
}

@font-face {
  font-family: "FOT-RodinProUB";
  src: local("FOT-RodinProUB"), url("../fonts/FOT-RodinProUB.otf") format("truetype");
}

@font-face {
  font-family: "FOT-SeuratProUB";
  src: local("FOT-SeuratProUB"), url("../fonts/FOT-SeuratProUB.otf") format("truetype");
}

h1, h2, h3, h4, h5 {
  font-family: "FOT-SeuratProUB";
}

h1 {
  font-size: 76px;
  margin-top: 0;
  margin-bottom: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
  align-items: center;
}

h2 {
  font-size: 36px;
}

h3 {
  font-weight: 400;
  font-size: 26px;
}

h5 {
  font-size: 18px;
}

.wavy {
  animation-name: wavy-anim;
  animation-duration: 1.2s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
}

.letter {
    position: relative;
    white-space: pre;
    cursor: default;
}

.hover-letter {
  transition: 0.2s;
  transition-timing-function: ease-out;
}

.hover-letter:hover {
  text-shadow: 3px 3px #ff7369, 5px 5px #343633;
  -webkit-text-stroke-color: white;
  -webkit-text-stroke-width: 0.15vw;
  transform: translateY(-10px);
}

.wavy-wrap {
  display: inline-block;
  overflow-wrap: break-word;
}

.important {
  /*color: #2ba4c2;*/
  color: #7D70BA;
}

.description {
  text-align: left;
  margin-top: 1vw;
  margin-bottom: 1vw;
  color: #343633;
}

.align-center {
  text-align: center;
}

.image-description {
  font-size: 0.8vw;
}

.letter {
  display: inline-block;
  line-height: 1em;
}

a {
  text-decoration: underline;
  color: #5e09e7;
}

a:visited {
  color: rgb(90, 90, 90);
}

@keyframes wavy-anim {
    0% {
        bottom: 0px;
    }
    50% {
        bottom: 7px;
    }
    100% {
        bottom: 0px;
    }
}

.header {
  color: #343633;
}

.subheader {
  color: #343633;
  margin-top: 0;
  margin-bottom: 0;
}


.cool-header {
  display: inline-block;
	letter-spacing:0.1em;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke-width: 3px;
  -webkit-text-stroke-color: white;
  text-shadow: 
						/*0.68vw 0.68vw #ff7369,*/
            5px 5px #ff7369,
						12px 12px #343633;
}

.cool-subheader {
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: #343633;
  text-shadow: 
						0px 0px #ff7369;
}

.subheader-wrap {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.project-year {
  position: relative;
  bottom: 30px;
  font-size: 22px;
}