/* DOM element styles */

html {
  scroll-behavior: smooth;
}

body {
  font-family: "FOT-RodinProUB";
  font-size: 16px;
  /*background: linear-gradient(0deg, rgb(13, 13, 63) 10%, rgb(39, 0, 146) 25%, rgb(4, 7, 179) 33%);*/
  background: rgb(255, 255, 255);
}

/* Scrolling background animation */

.scrolling-background {
  background-repeat: repeat;
  margin-left: 2vw;
  margin-right: 2vw;
  margin-bottom: 6vh;
}

.section3 {
  /*
  background: url('../media/chat_pattern_diag.png');
  background-color: #ff8de6;
  background-size: 80px 80px;
  animation: scrolling 2s linear infinite;
  */
  background-color: #5CC8FF;
}


@keyframes scrolling {
  from {
    background-position: 0 0;
  }
  to {
    background-position: 80px 80px;
  }
}

/* General page-related styles */

.page-container {
  display: flex;
  flex-direction: column;
  row-gap: 40px;
  /*background-image: url('../media/wood.png');*/
  padding-left: 2%;
  padding-right: 2%;
  padding-top: 2%;
  padding-bottom: 2%;
  border-radius: 60px;
  /*border: 15px solid rgb(41, 27, 9);*/
}

.parallax {
  background: linear-gradient(0deg, rgba(0, 0, 0, 0) 30%, #5CC8FF 50%, rgba(0, 0, 0, 0) 65%);
  height: 100vh;
  min-height: 100%;
  background-attachment: scroll, fixed;
  background-repeat: no-repeat;
  background-size: contain;
  background-color: transparent;
  background-position-x:center;
}

.header-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.section {
  /*
  width: 100%;
  padding-bottom: 2%;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  border-radius: 60px;
  border: 10px solid #ebfeeb;
  gap: 20px;
  */
  border-radius: 10px;
}

.content-container {
  display: flex;
  flex-direction: column;
  padding: 2% 4%;
  justify-content: center;
}

.body-bubble {
  /*
  background: #d5ece4d2;
  border: 20px solid #ebfeeb;
  border-radius: 10vw;
  padding: 3% 3%;
  */
  background: white;
  padding: 30px 30px;
  border-radius: 10px;
}

.shadow {
  box-shadow: 10px 10px 15px rgba(0,0,0,0.2);
  /*border: 4px solid rgb(0, 0, 0);*/
}

.short-shadow {
  box-shadow: 5px 5px 15px rgba(0,0,0,0.2);
  /*
  box-shadow: 5px 5px rgba(0,0,0,0.2);
  */
}

.clickable {
  cursor: pointer;
}

.section-header-wrap {
  z-index: 1;
  background: #fff;
  border-radius: 100px;   
  width: 50%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
  margin-bottom: 5px;
  box-sizing: border-box;
  border: 4px solid #7D70BA;
}

.section-header {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
  white-space: nowrap;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: black;
  text-shadow: 
						0px 0px #ff7369;
}

.subsection-header-wrap {
  
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  background-color: #7D70BA;
  top: 30px;
  height: 50px;
  color: white;
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: black;
  
}

.visible {
  visibility: visible;
  /*max-height: 10000px;*/
}

.hidden {
  visibility: hidden;
  /*max-height: 0;*/
}

.collapsible-button {
  background-image: url('../media/arrow.png');
  background-size: cover;
  background-position: center;
  width: 100px;
  height: 100px;
}

.front {
  z-index: 1;
  position: relative;
}


 /* 
  * ===================
  * Boiler plate styles 
  * ===================
  */
.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


.updown_hover {
  text-align: center;
}